import React, { useEffect, useState } from 'react';
import './Banner.css';
import banner from '../images/final_banner.jpg';

interface BannerProps {
  isVisible: boolean;
  onClose: () => void;
}

const Banner: React.FC<BannerProps> = ({ isVisible, onClose }) => {
  const [showBanner, setShowBanner] = useState(false);
  
  useEffect(() => {
    // Small delay to allow for animation
    if (isVisible) {
      const timer = setTimeout(() => {
        setShowBanner(true);
      }, 500);
      return () => clearTimeout(timer);
    } else {
      setShowBanner(false);
    }
  }, [isVisible]);

  if (!isVisible) return null; // Only show if visible

  const handleClose = () => {
    setShowBanner(false);
    // Small delay to allow for animation before fully closing
    setTimeout(() => {
      onClose();
    }, 300);
  };

  return ( 
    <>
      {/* 
        This backdrop dims or blocks out the underlying page.
        Clicking it can also close the modal if desired.
      */}
      <div
        className={`banner-backdrop ${showBanner ? 'show' : ''}`}
        onClick={handleClose}
      />

      {/* The banner modal itself */}
      <div className={`banner ${showBanner ? 'show' : ''}`}>
        <button
          className="close-button"
          onClick={handleClose}
          aria-label="Close"
        >
          <span>&times;</span>
        </button>
        <h5 className='alert'><i>Franklin County's Early Voting begins October 7th at their Morse Road location. Click <a target="_blank" rel="noopener noreferrer"  href='https://vote.franklincountyohio.gov/Voters/Absentee-Voting'>here</a> for more details.</i></h5>
        <div className="banner-content">
          {/* Use a normal <img> so the banner appears at full width */}
          <img
            src={banner}
            alt="Andrew Nimely Full Banner"
            className="banner-img"
          />

          {/* Optional text or headings
          <h1>Elect Andrew Nimely for Madison Township Trustee</h1>
          <h2>He Cares. He Listens. He Acts.</h2>
          <p>Paid for by Friends of Andrew Nimely</p>
          */}
        </div>
      </div>
    </>
  );
};

export default Banner;
