import React from 'react';
import './ThankYouPage.css';

const ThankYouPage = () => {
  // Get donor information from URL parameters
  const searchParams = new URLSearchParams(window.location.hash.split('?')[1]);
  const donorName = searchParams.get('name');
  const donorEmail = searchParams.get('email');
  return (
    <section className="thank-you-section no-zoom">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-md-8">
            <div className="thank-you-card">
              <div className="text-center mb-5">
                <h1 className="display-4 mb-3">Thank You for Your Support!</h1>
                <div className="checkmark-circle">
                  <div className="checkmark"></div>
                </div>
                <p className="lead mt-4">
                  Your contribution to Friends of Andrew B. Nimely is greatly appreciated.
                </p>
              </div>

              <div className="confirmation-details mb-5">
                <h3 className="text-center mb-4">Confirmation Details</h3>
                <div className="alert alert-success">
                  {donorName && (
                    <p className="mb-2">
                      Thank you, <strong>{donorName}</strong>, for your generous contribution!
                    </p>
                  )}
                  {donorEmail && (
                    <p className="mb-2">
                      A confirmation email has been sent to <strong>{donorEmail}</strong> with your donation details.
                    </p>
                  )}
                  <p className="mb-0">
                    Please check your inbox (and spam folder) for the receipt of your contribution.
                  </p>
                </div>
              </div>

              <div className="next-steps">
                <h4 className="text-center mb-3">What's Next?</h4>
                <div className="row g-4">
                  <div className="col-md-6">
                    <div className="next-step-card">
                      <h5>Stay Connected</h5>
                      <p>Follow our campaign progress and get involved in upcoming events.</p>
                      <a href="/" className="btn btn-outline-primary">Return to Homepage</a>
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="next-step-card">
                      <h5>Spread the Word</h5>
                      <p>Share our message with your friends and family to expand our reach.</p>
                      <a href="/contact" className="btn btn-outline-primary">Contact Us</a>
                    </div>
                  </div>
                </div>
              </div>

              <div className="donation-info mt-5">
                <p className="text-center">
                  <small>Paid for by Friends of Andrew B. Nimely</small>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ThankYouPage;
