import React, { Suspense, useState, useEffect } from 'react';
import { HashRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import AboutPage from './pages/AboutPage';
import ThankYouPage from './pages/ThankYouPage';
import Banner from './components/Banner';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import './App.css';

// Lazy load page components
const HomePage = React.lazy(() => import('./pages/HomePage'));
const PrioritiesPage = React.lazy(() => import('./pages/PrioritiesPage'));
const DonatePage = React.lazy(() => import('./pages/DonatePage'));
const ContactPage = React.lazy(() => import('./pages/ContactPage'));

// Create a component that uses the location hook
const AppContent = () => {
  const location = useLocation();
  const [showBanner, setShowBanner] = useState(false);

  useEffect(() => {
    // Only show banner on homepage
    setShowBanner(location.pathname === '/');
  }, [location.pathname]);

  const handleCloseBanner = () => {
    setShowBanner(false);
  };

  return (
    <div className="app">
      <Banner isVisible={showBanner} onClose={handleCloseBanner} />
      <Navbar />
      <Suspense
        fallback={
          <div className="loading-spinner">
            <div className="spinner-border text-primary" role="status">
              <span className="visually-hidden">Loading...</span>
            </div>
          </div>
        }
      >
        <main>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/about" element={<AboutPage />} />
            <Route path="/priorities" element={<PrioritiesPage />} />
            <Route path="/donate" element={<DonatePage />} />
            <Route path="/contact" element={<ContactPage />} />
            <Route path="/thank-you" element={<ThankYouPage />} />
          </Routes>
        </main>
      </Suspense>
      <Footer />
    </div>
  );
};

const App = () => {
  // Use HashRouter instead of BrowserRouter
  return (
    <Router>
      <AppContent />
    </Router>
  );
};

export default App;
